export default ()=> {
      return (
        <div className="question" data-index="46" dangerouslySetInnerHTML = {{ __html: `
<h6>46. 输出是什么?</h6>
<pre class="language-javascript"><code class="language-javascript"><span class="token keyword keyword-let">let</span> person <span class="token operator">=</span> <span class="token punctuation">{</span> name<span class="token operator">:</span> <span class="token string">"Lydia"</span> <span class="token punctuation">}</span><span class="token punctuation">;</span>
<span class="token keyword keyword-const">const</span> members <span class="token operator">=</span> <span class="token punctuation">[</span>person<span class="token punctuation">]</span><span class="token punctuation">;</span>
person <span class="token operator">=</span> <span class="token keyword keyword-null">null</span><span class="token punctuation">;</span>

console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span>members<span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre>
<ul>
<li>A: <code>null</code></li>
<li>B: <code>[null]</code></li>
<li>C: <code>[{}]</code></li>
<li>D: <code>[{ name: &quot;Lydia&quot; }]</code></li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: D</h4>
<p>首先我们声明了一个拥有<code>name</code>属性的对象 <code>person</code>。</p>
<img src="https://i.imgur.com/TML1MbS.png" width="200">
<p>然后我们又声明了一个变量<code>members</code>. 将首个元素赋值为变量<code>person</code>。 当设置两个对象彼此相等时，它们会通过 <em>引用</em> 进行交互。但是当你将引用从一个变量分配至另一个变量时，其实只是执行了一个 <em>复制</em> 操作。（注意一点，他们的引用 <em>并不相同</em>!）</p>
<img src="https://i.imgur.com/FSG5K3F.png" width="300">
<p>接下来我们让<code>person</code>等于<code>null</code>。</p>
<img src="https://i.imgur.com/sYjcsMT.png" width="300">
<p>我们没有修改数组第一个元素的值，而只是修改了变量<code>person</code>的值,因为元素（复制而来）的引用与<code>person</code>不同。<code>members</code>的第一个元素仍然保持着对原始对象的引用。当我们输出<code>members</code>数组时，第一个元素会将引用的对象打印出来。</p>
</p>
</details>
` }}></div>
      );
    }